<template>
  <div class="form-register px-5">
    <div class="form-head">
      <h1>Register</h1>
      <hr />
    </div>
    <div class="form-body mt-5">
      <form>
        <div class="form-group">
          <div class="mt-3">
            <label class="text-left d-block" for="form-username">Username</label>
            <input
              type="text"
              class="form-control"
              v-model="form.username"
              id="form-username"
              aria-describedby="emailHelp"
            />
          </div>
          <div class="mt-3">
            <label class="text-left d-block" for="form-email">Email address</label>
            <input
              type="email"
              v-model="form.email"
              class="form-control"
              id="form-email"
              aria-describedby="emailHelp"
            />
          </div>
          <div class="mt-3">
            <label
              class="text-left d-block"
              :class="{ 'error-text' : error.password }"
              for="form-password"
            >Password</label>
            <input
              type="password"
              v-model="form.password"
              :class="{ 'error-input' : error.password }"
              class="form-control"
              id="form-password"
              aria-describedby="emailHelp"
            />
            <p class="error-text d-block text-left mt-1" v-if="error.password">{{error.password}}</p>
          </div>
          <div class="mt-3">
            <label
              class="text-left d-block"
              :class="{'error-text ': error.confirmPassword}"
              for="form-confirm-password"
            >Confirm Password</label>
            <input
              type="password"
              class="form-control"
              :class="{'error-input' : error.confirmPassword }"
              id="form-confirm-password"
              v-model="form.confirmPassword"
              aria-describedby="emailHelp"
            />
            <p
              class="error-text d-block text-left mt-1"
              v-if="error.confirmPassword"
            >{{error.confirmPassword}}</p>
          </div>
        </div>
        <div class="form-img">
          <label class="text-left d-block" for="image">Image</label>
          <input type="file" class="d-block" name ref="file" @change="handleUpload" id="image" />
          <img src id="avatar" alt />
        </div>
        <div v-if="error.show" class="error">
          <h5
            class="text-white d-block text-left p-2 bg-danger"
            v-for="(error,index) in error.show"
            :key="index"
          >{{error}}</h5>
        </div>
        <button
          type="submit"
          @click="submitClick"
          :disabled="isError"
          class="btn btn-primary mt-2"
        >Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { addVisitor } from "@/utils/index";

export default {
  name: "FormRegister",
  data() {
    return {
      form: {
        username: "",
        email: "",
        password: "",
        confirmPassword: "",
        avatar: "",
      },
      error: {
        password: "",
        confirmPassword: "",
        req: "",
        show: [],
      },
    };
  },
  watch: {
    "form.password": function (oldVal, newVal) {
      if (newVal.length >= 6) {
        this.error.password = "";
      } else {
        this.error.password = "Passoword harus lebih dari 6 karakter";
      }
    },
    "form.confirmPassword": function (val) {
      if (val === this.form.password) {
        this.error.confirmPassword = "";
      } else {
        this.error.confirmPassword = "Password tidak sama";
      }
    },
    "error.req": function (val) {
      const errorEmail = val.toString().split(":").indexOf(` email_1 dup key`);
      console.log(errorEmail);
      if (errorEmail >= 0) {
        return this.error.show.push("Email sudah terdaftar");
      } else {
        const error = val
          .toString()
          .split(":")
          .splice(4)
          .toString()
          .split(",")
          .filter((e, index) => {
            return index % 2 != 0;
          });
        return (this.error.show = error);
      }
    },
  },
  inject : ['loading'],
  methods: {
    ...mapActions(["userRegister"]),
    async submitClick(e) {
      e.preventDefault();
      if (this.form.password == 0) {
        return (this.error.reqArr = [
          ...this.error.reqArr,
          "Password Required",
        ]);
      }
      try {
        this.loading.value = true;
        await this.userRegister(this.form);
        await addVisitor();
        setTimeout( () =>{
          this.loading.value = false;
          return this.$router.replace({ name: "home" });
        },1000)
      } catch (err) {
        this.loading.value = false;
        this.error.req = err;
        console.log(err);
      }
    },
    handleUpload() {
      const file = this.$refs.file.files[0];
      const avatar = document.getElementById("avatar");
      avatar.src = URL.createObjectURL(file);
      this.form.avatar = file;
      avatar.onload = () => {
        URL.revokeObjectURL(avatar.src);
      };
    },
  },
  computed: {
    isError() {
      // const email = this.form.email;
      // const isValid = /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
      // console.log(isValid);
      if (
        this.error.password ||
        this.error.confirmPassword ||
        this.form.password.length == 0 ||
        this.form.confirmPassword.length == 0
      ) {
        return true;
      } 
      // else if(!isValid) {
      //   this.error.show = 'Email is not valid';
      // } 
      else {
        return false;
      }
    },
  },
};
</script>

